<template>
<div class="j-tab-pane" v-show="show && $slots.default">
  {{show}}
  <slot></slot>
</div>
</template>

<script>
export default {
  props: {
    label: {
      required: true,
      type: String
    },
    id: {
      type: String
    },
    lock: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    show() {
      return this.$parent.currentId === this.id
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/styles/mixins.less';
</style>



