<script>
export default {
  props: {
    activeId: {
      type: String
    }
  },
  data() {
    return {
      currentId: '',
      removeTabs: []
    }
  },
  mounted() {
    this.currentId = this.activeId || this.$children[0].id || ''
  },
  methods: {
    handleTabClick($event, tab) {
      this.currentId = tab.id
    },
    handleTabRemove($event, tab) {
      tab.$destroy(true)
      // if (this.removeTabs.indexOf(tab.id) === -1) {
      //   this.removeTabs.push(id)
      // }
    }
  },
  render(h) {
    let { currentId } = this
    const tabs = this.$children.map((tab, index) => {
      const { id, label, lock } = tab
      const $closeBtn = h('a', {
        class: {
          'close': true
        },
        on: {
          click: $event => {
            this.handleTabRemove($event, tab)
          }
        }
      }, [
        'x'
      ])
      const $tab = h('span', {
        class: {
          'j-tabs__header__label': true,
          'active': currentId === id
        },
        on: {
          click: $event => {
            this.handleTabClick($event, tab)
          }
        }
      }, [
        label,
        lock ? null : $closeBtn
      ])
      return $tab
    })
    return (
      <section class="j-tabs">
        <div class="j-tabs__header">
          {tabs}
        </div>
        <div class="j-tabs__content">
          {this.$slots.default}
        </div>
      </section>
    )
  }
}
</script>

<style lang="less" scoped>
.j-tabs {
  border: 1px solid #ccc;
}
.j-tabs__header__label {
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  padding: 5px 20px;
  position: relative;
  &:hover {
    .close {
      opacity: 1;
      z-index: auto;
    }
  }
  &.active {
    background-color: #f33;
    color: #fff;
  }
  .close {
    background-color: #f33;
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    font-weight: bold;
    height: 16px;
    line-height: 16px;
    margin-top: 5px;
    opacity: 0;
    position: absolute;
    right: 3px;
    text-align: center;
    top: 0;
    transition: all .2s;
    width: 16px;
    z-index: -1;
  }
}
</style>



