
<script>
import classNames from 'classnames'
export default {
  props: {
    selectable: {
      type: Boolean,
      default: true
    },
    pageable: {
      type: Boolean,
      default: true
    },
    multiple: {
      type: Boolean,
      default: true
    },
    pageSize: {
      type: Number,
      default: 10
    },
    listData: {
      type: Object
    },
    beforeRender: {
      type: Function
    },
    loading: {
      type: Boolean,
      default: false
    },
    fixRows: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      empty: false,
      allChecked: false,
      columns: [],
      internalData: [],
      pagination: {
        totalPages: 0,  // 总页数
        totalElements: 0, // 总条数
        last: false, // 是否最后一页
        number: 0, // 当前页 从0开始
        sort: null, // 排序
        numberOfElements: 0, // 实际多少个
        first: false // 是否第一页
      }
    }
  },
  watch: {
    listData(val) {
      if (this.pageable) {
        this.internalData = val.content
        this.pageSize = val.size
        this.pagination.totalPages = val.totalPages
        this.pagination.totalElements = val.totalElements
        this.pagination.last = val.last
        this.pagination.number = val.number
        this.pagination.sort = val.sort
        this.pagination.numberOfElements = val.numberOfElements
        this.pagination.first = val.first
      } else {
        this.internalData = val
      }
      if (this.selectable) {
        this.internalData.forEach(row => {
          row.$checked = false
        })
        if (this.allChecked) { // 重置全选状态
          this.allChecked = false
        }
      }
    }
  },
  methods: {
    handleCheckAll() {
      if (!this.internalData.length) {
        return
      }
      this.allChecked = !this.allChecked
      this.internalData.forEach(row => {
        row.$checked = this.allChecked
      })
      this.$emit('all-select', this.allChecked)
    },
    handleCheck(row) {
      if (this.multiple) {
        row.$checked = !row.$checked
        // 联动全选
        this.allChecked = this.internalData.every(row => row.$checked)
      } else {
        this.internalData.forEach(row => {
          row.$checked = false
        })
        row.$checked = true
      }
      this.$forceUpdate()
      this.$emit('select', row, row.$checked)
    },
    getColumns(defaultSlots = []) {
      return defaultSlots.filter(({componentOptions}) => componentOptions && componentOptions.tag === 'j-grid-column').map(({componentOptions: {propsData}}) => {
        return propsData
      })
    },
    renderColumns(h, columns) {
      const $columns = columns.map(column => {
        let style = null
        if (column.width) {
          style = `width:${column.width}`
        }
        return (<th style={style}>{column.label}</th>)
      })
      if (this.selectable && this.multiple) {
        const checkboxClass = classNames('checkbox-wrap', {
          checked: this.allChecked
        })
        $columns.unshift(<th><label class="grid-checkbox" on-click={$event => this.handleCheckAll()}><span class={checkboxClass}></span></label></th>)
      }
      return (<tr>{$columns}</tr>)
    },

    renderRow(h, columns, row, fill = false) {
      const $cells = columns.map(column => {
        const cellValue = row[column.code]
        let $cell = null
        // 外层渲染
        if (this.beforeRender) {
          $cell = this.beforeRender(h, {
            code: column.code,
            value: cellValue
          })
        }
        // 默认渲染
        if (!$cell) {
          $cell = (<span>{cellValue}</span>)
        }
        return (<td>{$cell}</td>)
      })
      if (this.selectable && this.multiple) {
        const checkboxClass = classNames('checkbox-wrap', {
          checked: row.$checked
        })
        $cells.unshift(<td><label class="grid-checkbox" on-click={$event => this.handleCheck(row)}><span class={checkboxClass}></span></label></td>)
      }
      const rowClass = classNames({
        checked: row.$checked
      })
      let $row = null
      if (this.selectable && !this.multiple) {
        $row = (<tr on-click={$event => this.handleCheck(row)} class={rowClass}>{$cells}</tr>)
      } else {
        $row = (<tr class={rowClass}>{$cells}</tr>)
      }
      return $row
    },

    renderFillRow(h, columnLength) {
      const $cells = []
      while (columnLength--) {
        $cells.push(<td>&nbsp;</td>)
      }
      return (<tr>{$cells}</tr>)
    },

    renderRows(h, columns) {
      const $rows = this.internalData.map(row => this.renderRow(h, columns, row))
      // fill blank rows
      const { numberOfElements } = this.pagination
      if (numberOfElements && this.pageable && this.fixRows) {
        if (numberOfElements < this.pageSize) {
          let fillRowCount = this.pageSize - numberOfElements
          let columnLength = columns.length
          if (this.selectable && this.multiple) {
            columnLength++
          }
          while (fillRowCount--) {
            $rows.push(this.renderFillRow(h, columnLength))
          }
        }
      }
      return $rows
    },
    renderPagination(h) {
      if (!this.pageable) {
        return null
      }
      const {first, last, totalPages, number, totalElements} = this.pagination
      // 分页数量
      const $sizes = [10, 20, 50, 100].map(index => {
        return (<option value={index} selected={index === this.pageSize}>{index}</option>)
      })

      // 页数 计算隐藏页
      const $pages = []
      let showPages = 10
      let start = 0
      let end = totalPages
      if (totalPages > showPages) {
        start = end = number
        showPages--
        while (showPages) {
          if (showPages && start !== 0) {
            start--
            showPages--
          }
          if (showPages && end !== totalPages - 1) {
            end++
            showPages--
          }
        }
      }
      let pageIndex = 0
      while (pageIndex < totalPages) {
        if (start <= pageIndex && end >= pageIndex) {
          (index => {
            const buttonClass = classNames({
              active: index === number
            })
            $pages.push(<li><button type="button" class={buttonClass} on-click={$event => this.$emit('page-change', index)}>{index + 1}</button></li>)
          })(pageIndex)
        }
        pageIndex++
      }

      // 上一页、下一页
      if (totalPages) {
        $pages.unshift(<li class="prev"><button type="button" disabled={first} on-click={$event => this.$emit('page-change', number - 1)}>&lt;</button></li>)
        $pages.push(<li class="next"><button type="button" disabled={last} on-click={$event => this.$emit('page-change', number + 1)}>&gt;</button></li>)
      }
      return (
        <div class="pagination clearfix">
          <div class="pull-left">
            共<span class="total">{totalElements}</span>条数据，每页显示
            <select class="form-control" on-change={({target: {value}}) => this.$emit('size-change', parseInt(value, 10))}>
              {$sizes}
            </select>条记录
          </div>
          <div class="pull-right">
            <ul class="pages list-unstyled">
              {$pages}
            </ul>
          </div>
        </div>
      )
    }
  },
  render(h) {
    const columns = this.getColumns(this.$slots.default)
    const gridClass = classNames('grid', {
      multiple: this.multiple,
      selectable: this.selectable,
      loading: this.loading
    })
    return (
      <div class={gridClass}>
        <div class="layer"></div>
        <table>
          <thead>
            {this.renderColumns(h, columns)}
          </thead>
          <tbody>
            {this.renderRows(h, columns)}
          </tbody>
        </table>
        {this.renderPagination(h)}
      </div>
    )
  }
}
</script>

<style lang="less" scoped>
.grid-checkbox {
  display: inline-block;
  height: 14px;
  width: 14px;
  overflow: hidden;
  .checkbox-wrap {
    display: inline-block;
    background-color: #fff;
    border: 1px solid #e4e4dc;
    height: 100%;
    width: 100%;
    &::after {
      background-color: #4caf50;
      content: " ";
      display: inline-block;
      margin: 1px;
      height: 10px;
      width: 10px;
      opacity: 0;
      cursor: pointer;
    }
    &.checked {
      &::after {
        transition: all .3s;
        opacity: 1;
      }
    }
  }
}
.grid {
  margin-top: 10px;
  &.selectable:not(.multiple) {
    td {
      cursor: pointer;
    }
    tr.checked td {
      background-color: #eff7ff;
    }
  }
  table {
    border-spacing: 0;
    width: 100%;
  }
  tbody tr:hover {
    td {
      transition: all .3s;
      background-color: #f5f4f1;
    }
  }
  th, td {
    padding: 8px 12px;
    text-align: center;
  }
  th {
    background-color: #fbfaf7;
    border: 1px solid #e4e4dc;
    color: #333;
    font-size: 14px;
    &:not(:first-child) {
      border-left: 0;
    }
    &:not(:last-child) {
      border-right: 0;
    }
  }
  tr {
    td {
      color: #666;
      font-size: 12px;
      border: 1px solid #f2f1ec;
      border-top: 0;
      &:not(:first-child) {
        border-left: 0;
      }
    }
  }
  .layer {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: url(../assets/images/loading.gif) no-repeat center center rgba(192, 192, 192, .5);
    z-index: -1;
    opacity: 0;
  }
  &.loading {
    position: relative;
    .layer {
      transition: all .3s;
      z-index: 1;
      opacity: 1;
    }
  }
  .pagination {
    font-size: 12px;
    margin-top: 10px;
    select {
      padding: 3px 6px;
      text-align: center;
      margin: 0 5px;
      outline: 0;
    }
    .total {
      color: #f33;
      padding: 0 5px;
    }
    .pages {
      display: inline-block;
      .prev, .next {
        button {
          font-weight: bold;
        }
      }
      li {
        display: inline-block;
        margin: 2px;
        button {
          outline: 0;
          padding: 0;
          cursor: pointer;
          display: inline-block;
          border: 1px solid #e5e5e5;
          background-color: #fafafa;
          width: 22px;
          height: 22px;
          line-height: 22px;
          text-align: center;
          font-size: 14px;
          color: #666;
          transition: all .3s;
          &[disabled] {
            opacity: .8;
            cursor: not-allowed;
          }
          &:hover:not([disabled]) {
            border-color: #f24f44;
            color: #f24f44;
          }
          &.active {
            border-color: #f24f44!important;
            background-color: #f48565!important;
            color: #fff!important;
          }
        }
      }
    }
  }
}
</style>



