<template>
<section class="app-main">
  <div v-if="tabs.length" class="tabs-wrap">
    <div class="tabs-header">
      <ul class="list-unstyled">
        <li v-for="tab in tabs" @click="activeTab(tab.seqId)" :class="{active: tab.seqId === currentTab}">
          <a>{{tab.name}}</a>
          <i v-if="!tab.lock" class="el-icon-close" @click.stop="removeTab(tab.seqId)"></i>
        </li>
      </ul>
      <div v-show="scrollButton.show" class="oper-list">
        <button type="button" :disabled="scrollButton.disabledLeft" @click="scroll(true)"><i class="icon el-icon-caret-left"></i></button>
        <button type="button" :disabled="scrollButton.disabledRight" @click="scroll(false)"><i class="icon el-icon-caret-right"></i></button>
      </div>
    </div>
    <ul class="tabs-content list-unstyled">
      <li v-for="tab in tabs" v-show="tab.seqId === currentTab" class="content-wrap">
        <div :is="tab.id" :params="tab.params"></div>
      </li>
    </ul>
  </div>
  <div v-else class="empty-tabs">
    ......
  </div>
</section>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'
import { appFrameTypes } from 'store/types'

export default {
  computed: {
    ...mapState({
      tabs: state => state.appFrame.tabs,
      currentTab: state => state.appFrame.currentTab,
      scrollButton: state => state.appFrame.scrollButton
    })
  },
  methods: {
    ...mapActions({
      removeTab: appFrameTypes.A.REMOVE_TAB,
      scroll: appFrameTypes.A.SCROLL
    }),
    ...mapMutations({
      activeTab: appFrameTypes.ACTIVE_TAB
    })
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/styles/mixins.less';
@import '../../assets/styles/colors.less';

@tabHeight: 40px;
section {
  height: 100%;
  width: 100%;

}
.tabs-wrap {
  height: 100%;
  position: relative;
  padding-top: 10px;
}
.tabs-header {
  padding: 0 10px;
  white-space: nowrap;
  border-bottom: 1px solid @grey300;
  position: relative;
  padding-right: 50px;
  .oper-list {
    .absolute(10px, 10px, auto, auto);
    background-color: @white;
    button {
      background-color: @white;
      border: 1px solid @grey300;
      display: inline-block;
      padding: 0;
      outline: none;
      text-align: center;
      cursor: pointer;
      &[disabled] {
        opacity: .5;
        cursor: auto;
        &:hover .icon{
          color: @blue;
        }
      }
      &:hover .icon {
        transition: all .3s;
        color: @blue800;
      }
    }
    .icon {
      font-size: 12px;
      color: @blue;
    }
  }
  ul {
    overflow: hidden;
    &.full {
      border-right: 1px solid @grey300;
    }
  }
  li {
    display: inline-block;
    color: @grey700;
    background-color: @grey50;
    transition: background-color .3s;
    cursor: pointer;
    border-right: 1px solid @grey300;
    border-top: 1px solid @grey300;
    display: inline-block;
    padding: 0 15px;
    font-size: 14px;
    padding: 10px;
    position: relative;
    &.active {
      color: @black;
      border-top: 2px solid @blue;
    }
    &:first-child {
      border-left: 1px solid @grey300;
    }
    .el-icon-close {
      padding-left: 8px;
      font-size: 12px;
      color: @grey700;
      transition: all .2s;
      cursor: pointer;
      &:hover {
        color: @black;
      }
    }
  }
}
.tabs-content {
  .absolute(@tabHeight + 10, 0, 0, 0);
  li.content-wrap {
    height: 100%;
    padding: 10px;
    overflow: auto;
  }
}
</style>



