<template>
<section class="app-menu">
  <div class="menu-tabs-title">功能导航</div>
  <ul class="list-unstyled fixed-menu">
    <li class="clearfix">
      <div class="col-6" @click="addTab('home')">
        <div class="text-center"><i class="iconfont icon-shouyeshouye"></i></div>
        <div class="text-center">系统首页</div>
      </div>
      <div class="col-6" @click="addTab('repository')">
        <div class="text-center"><i class="iconfont icon-zhishiku"></i></div>
        <div class="text-center">知识库</div>
      </div>
    </li>
    <li class="clearfix">
      <div class="col-6" @click="addTab({link: 'http://www.jd.com/'})">
        <div class="text-center"><i class="iconfont icon-jingdong1"></i></div>
        <div class="text-center">京东首页</div>
      </div>
      <div class="col-6" @click="addTab({link: 'http://www.kuaidi100.com/?from=openv'})">
        <div class="text-center"><i class="iconfont icon-kuaidi100"></i></div>
        <div class="text-center">快递100</div>
      </div>
    </li>
  </ul>
  <div v-show="currentTab === 'access'" class="access-wrap">接入信息...</div>
  <div v-show="currentTab === 'nav'" class="fun-menu">
    <ul class="list-unstyled top-fun-menu">
      <li>
        <div class="top-name clearfix" @click="showCompose = !showCompose">
          <div class="col-4">
            <i class="icon el-icon-menu"></i>
          </div>
          <div class="col-6">
            综合处理
          </div>
          <div class="col-2">
            <i class="icon" :class="{'el-icon-arrow-up': showCompose, 'el-icon-arrow-down': !showCompose}"></i>
          </div>
        </div>
        <ul v-show="showCompose" class="list-unstyled child-fun-menu">
          <li><i class="icon line"></i><a @click="addTab('unify')"><i class="icon yuan"></i>弹屏统一查办视图</a></li>
          <li><i class="icon line"></i><a @click="addTab('order')"><i class="icon yuan"></i>订单类业务视图</a></li>
          <li><i class="icon line"></i><a @click="addTab('account')"><i class="icon yuan"></i>账户类业务视图</a></li>
          <li><i class="icon line"></i><a @click="addTab('query')"><i class="icon yuan"></i>辅助查询视图</a></li>
        </ul>
      </li>
      <li>
        <div class="top-name clearfix"  @click="showWorkbench = !showWorkbench">
          <div class="col-4">
              <i class="iconfont icon-gongzuotai"></i>
          </div>
          <div class="col-6">
            个人工作台
          </div>
          <div class="col-2">
            <i class="icon" :class="{'el-icon-arrow-up': showWorkbench, 'el-icon-arrow-down': !showWorkbench}"></i>
          </div>
        </div>
        <ul v-show="showWorkbench" class="list-unstyled child-fun-menu">
          <li><i class="icon line"></i><a @click="addTab('event-list')"><i class="icon yuan"></i>事件管理</a></li>
          <li><i class="icon line"></i><a @click="addTab('workOrder')"><i class="icon yuan"></i>工单管理</a></li>
          <li><i class="icon line"></i><a @click="addTab('afterSale')"><i class="icon yuan"></i>售后管理</a></li>
        </ul>
      </li>
    </ul>
  </div>
</section>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'
import { appFrameTypes, appMenuTypes } from 'store/types'
export default {
  data() {
    return {
      showCompose: true,
      showWorkbench: true
    }
  },
  computed: {
    ...mapState({
      currentTab: state => state.appMenu.currentTab
    })
  },
  mounted() {
    // 默认打开系统首页
    this.addTab('home')
  },
  methods: {
    ...mapActions({
      addTab: appFrameTypes.A.ADD_TAB
    }),
    ...mapMutations({
      activeAccess: appMenuTypes.ACTIVE_ACCESS,
      activeNav: appMenuTypes.ACTIVE_NAV
    })
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/styles/mixins.less';
@import '../../assets/styles/colors.less';
section {
  height: 100%;
  background-color: #f6f2ed;
}
.menu-tabs-title {
  font-size: 14px;
  border-bottom: 1px solid #ded9d3;
  padding: 10px;
  padding-left: 20px;
  color: #333;
}
.fixed-menu {
  li {
    border-bottom: 1px solid #ded9d3;
    color: #666;
    font-size: 14px;
    cursor: pointer;
    &>div {
      padding: 15px;
      div {
        padding: 1px;
      }
      &:first-child {
        border-right: 1px solid #ded9d3;
      }
      &:hover {
        transition: all .3s;
        background-color: #ede7e0;
      }
    }
  }
  .iconfont {
    color: #999;
    &.icon-shouyeshouye {
      color: #18baf5;
    }
  }
}

.access-wrap {
  font-size: 14px;
  padding-top: 5px;
}

.top-fun-menu {
  font-size: 14px;
  color: #333;
  background-color: #c6c0b3;
  cursor: pointer;
  &>li:not(:last-child) {
    border-bottom: 1px solid #ded9d3;
  }
  .iconfont, .icon {
    color: #666;
  }
  .top-name {
    padding: 15px 10px;
    &>div {
      &:first-child {
        padding-left: 20px;
      }
    }
  }
}

.child-fun-menu {
  background-color: #e7e4dc;
  margin-left: -4px;
  font-size: 12px;
  padding-left: 40px;
  li {
    display: block;
    padding: 10px;
    padding-left: 15px;
    border-left: 2px solid #c6c0b3;
    position: relative;
    &:last-child .line::before {
       content: ' ';
       width: 2px;
       height: 16px;
       display: inline-block;
       background-color: #e7e4dc;
       position: relative;
       top: 2px;
       left: -2px;
    }
    .line {
      display: inline-block;
      height: 2px;
      width: 10px;
      background-color: #c6c0b3;
      .absolute(17px, auto, auto, 0);
    }
    a {
      padding: 3px 5px;
      cursor: pointer;
      &:hover {
        transition: all .3s;
        background-color: #c6c0b3;
        border-radius: 10px;
        .yuan {
          transition: all .3s;
          border-color: #666;
        }
      }
      .yuan {
        display: inline-block;
        height: 12px;
        width: 12px;
        border-radius: 50%;
        border: 2px solid #c6c0b3;
        margin-right: 8px;
        position: relative;
        top: 2px;
      }
    }
  }
}
</style>



